/* styles.css */
.ant-layout-header {
  background: #fff;
  position: fixed;
  width: 100%;
  z-index: 1000;
}

.ant-layout-content {
  padding: 60px 50px;
  background: #f0f2f5;
}

.ant-layout-footer {
  text-align: center;
  background: #fff;
}

.element {
  padding: 50px 0;
}

.element > div {
  margin: 50px 0;
}
.section {
  padding: 50px 0; /* Adjust the value as needed */
}

.carousel-description {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  padding: 10px;
  text-align: center;
}

/* Carousel styles */
.ant-carousel .slick-slide {
  text-align: center;
  height: 400px; /* Adjust the height as needed */
  line-height: 400px; /* Same as height */
  background: #364d79;
  overflow: hidden;
}

.ant-carousel .slick-slide img {
  max-width: 100%;
  max-height: 100%;
  display: inline-block;
}

/* styles.css */
.legend {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  padding: 10px;
  text-align: center;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

/* Portfolio.css */
.carousel-container {
  max-width: 80vw;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.carousel {
  width: 100%;
  max-width: 800px;
  height: 800px;
  margin: 20px auto;
}

.carousel-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project-details {
  text-align: center;
  margin-bottom: 20px;
}

.image-container {
  width: 100%;
  height: 200px; /* Adjust the height as needed */
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px; /* Add border-radius to round the corners */
}

.image-container img {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 10px; /* Add border-radius to round the corners */
}

.purple-menu .ant-menu-item a {
  color: purple;
}

.purple-menu .ant-menu-item:hover, 
.purple-menu .ant-menu-item-active, 
.purple-menu .ant-menu-item-selected {
  background-color: #f0f0f0; /* Optional: change the background color on hover */
  color: purple;
}

.purple-menu .ant-menu-item:hover a, 
.purple-menu .ant-menu-item-active a, 
.purple-menu .ant-menu-item-selected a {
  color: purple;
}

#home {
  position: relative;
  width: 100%;
  min-height: 100vh;  /* Ensure the container covers the viewport height */
  overflow: hidden;   /* Prevents any overflow issues */
}

#home::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('../public/images/background.jpeg');  /* Replace with your background image URL */
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.2;  /* Adjust the opacity to make the image faint */
  z-index: -1;  /* Ensure the background is behind other content */
}



